.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;

  &__container {
    position: fixed;
    left: 50%;
    top: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    width: 100%;
    min-height: rem-calc(125);
    transform: translateX(-50%);
  }

  &__main {
    display: flex;
    align-items: center;

    @include lg-block {
      flex: 0 0 494 / 1240 * 100%;
    }
  }

  &__logo {
    margin-right: var(--s-24);
    font: var(--font-logo);
    color: var(--color-black);
  }

  &__search {
    margin-left: auto;
  }
}
